<template>
  <div class="step3-container">
    <div class="warn-con">
      <a-alert>DataKit</a-alert>
    </div>
    <div class="search-con">
      <a-form :model="form" layout="inline">
        <a-form-item field="ip">
          <a-input v-model="form.ip" allow-clear placeholder="IP"></a-input>
        </a-form-item>
        <a-form-item field="ip">
          <a-input v-model="form.ip" allow-clear placeholder=""></a-input>
        </a-form-item>
        <a-form-item field="ip">
          <a-input v-model="form.ip" allow-clear placeholder="cpu"></a-input>
        </a-form-item>
        <a-form-item field="ip">
          <a-input v-model="form.ip" allow-clear placeholder=""></a-input>
        </a-form-item>
        <a-form-item field="ip">
          <a-input v-model="form.ip" allow-clear placeholder=""></a-input>
        </a-form-item>
        <a-form-item>
          <a-button type="outline">
            <template #icon>
              <icon-search />
            </template>
            <template #default></template>
          </a-button>
          <a-button style="margin-left: 10px;">
            <template #icon>
              <icon-sync />
            </template>
            <template #default></template>
          </a-button>
        </a-form-item>
      </a-form>
    </div>
    <div class="table-con">
      <div class="select-tips">
        <span class="tips-item"><b>{{ props.subTaskConfig.length }}</b></span>
        <span class="tips-item"><b>{{ selectedKeys.length }}</b></span>
      </div>
      <a-table :loading="loading" row-key="hostId" :data="tableData" :row-selection="rowSelection" v-model:selectedKeys="selectedKeys" :bordered="false" stripe :pagination="pagination" @page-change="pageChange" @selection-change="selectionChange">
        <template #columns>
          <a-table-column title="IP" data-index="publicIp"></a-table-column>
          <a-table-column title="+OS" data-index="hostname"></a-table-column>
          <a-table-column title="">
            <template #cell="{ record }">
              {{ record.os ? '' + record.os + ',' : '' }}
              {{ record.os ? 'CPU' + record.cpuArch : '' }}
            </template>
          </a-table-column>
          <a-table-column title="" data-index="d"></a-table-column>
          <a-table-column title="" data-index="tasks" align="center">
            <template #cell="{ record }">
              {{ record.tasks.length }}
            </template>
          </a-table-column>
          <a-table-column title="" data-index="tasks">
            <template #cell="{ record }">
              {{ record.tasks.map(item => `#${item.id}`).join(', ')}}
            </template>
          </a-table-column>
        </template>
      </a-table>
    </div>
  </div>
</template>

<script setup>
import { reactive, ref, onMounted, toRaw } from 'vue'
import { hostsData } from '@/api/task'

const props = defineProps({
  subTaskConfig: {
    type: Array,
    default: () => []
  },
  hostData: {
    type: Array,
    default: () => []
  }
})

const emits = defineEmits(['syncHost'])

const loading = ref(true)
const form = reactive({
  ip: undefined
})

const queryParams = reactive({
  pageNum: 1,
  pageSize: 10
})
const pagination = reactive({
  total: 0,
  current: 1,
  pageSize: 10
})
const tableData = ref([])

const selectedKeys = ref([])
const rowSelection = reactive({
  type: 'checkbox',
  showCheckedAll: true,
  onlyCurrent: false
})

const pageChange = (current) => {
  queryParams.pageNum = current
  pagination.current = current
  // getList()
}

const selectionChange = (rowKey) => {
  emits('syncHost', rowKey)
}

const getHostsData = () => {
  loading.value = true
  hostsData().then(res => {
    loading.value = false
    tableData.value = res.data
    pagination.total = res.data.length
  }).catch(() => {
    loading.value = false
  })
}

onMounted(() => {
  getHostsData()
  selectedKeys.value = toRaw(props.hostData)
})
</script>

<style lang="less" scoped>
.step3-container {
  .warn-con {
    width: 70%;
    margin: 0 auto;
  }
  .search-con {
    margin-top: 20px;
    padding: 0 20px;
  }
  .table-con {
    margin-top: 20px;
    padding: 0 20px 30px;
    .select-tips {
      display: flex;
      margin-bottom: 10px;
      .tips-item {
        color: var(--color-text-1);
        margin-right: 10px;
      }
    }
  }
}
</style>
